@import "../../variables.scss";


.title-product{
  padding-top:30px; 
  h2{
    font-size:42px;
    font-weight: bold;
    color: $blue;
  }
  label{
    color: $yellow;
    font-size: 18px;
    font-weight: 600;
  }
}

.box-steps{
  border:1px solid $gray-bg;
  border-radius:5px;
  padding: 15px 40px;  
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  label{
    font-size: 18px;
    font-weight: 600;
    color: $gray-text;
    margin: 0;
  }
}

.number{
  background-color: $gray-bg;
  border-radius: 50%;
  color: $gray-text;
  font-size:20px;
  font-weight: 600;
  display: inline-block;
  width: 33px;
  text-align: center;
  height: 33px;
  margin-right:15px; 
  &.active{
    background-color: $yellow;
    color: white;
  }
}

.box-product{
  padding: 20px 40px;
  background-color: $gray-bg;
  .title-box{
    display: flex;
    justify-content: space-between;
    label{
      font-size: 24px;
      font-weight: 600;
      color: $blue;
    }
  }
}

.radio-tella{
  padding-bottom: 10px;
  .custom-control-label{
    padding-left: 10px;
    color: $gray-text;
    &::before{
      background: white;
      border: 1px solid $gray-border;
      top: 2px;
    }
  }
  .custom-control-input:checked~.custom-control-label{
    &::before{
      background-color: white!important ;
      width: 20px;
      height: 20px;
    }
    &::after{
      background-image: none !important;
      background: $yellow;
      border-radius: 50px;
      height: 10px;
      width: 10px;
      top: 7px;
      left: -19px;
    }
  }

}

form{
  .item-form{
    display: flex;
    margin: 0 -7px;
  }
}

.input-tella{
  flex:1;
  padding: 0 7px;
  position: relative;

  label{
    color: $gray-text;
  }
  input,textarea{
    border: 1px solid $gray-border;
    color: $gray-text;
    border-radius: 6px;
    &::placeholder {
      color:$gray-placeholder;
    }
    &:focus{
      box-shadow: none;
      border-color:$gray-text;
    }
  }
}

.input-icon{
  input{
    padding-right: 30px;
  }
  i{
    position: absolute;
    bottom: 7px;
    right: 15px;
    color: $yellow;
  }
}

.box-group{
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  .box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 150px;
    width: 160px;
    color: transparent;
    border-radius:6px;
    border:1px solid $gray-border;
    &.active{
      background: white;
      color: $yellow;
      cursor: pointer;
      i{
        font-size: 40px;
      }
    }   
  }
}

.box-upload{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  background: white;
  border:1px dashed $gray-border;
  color: $yellow;
  cursor: pointer;
}
